<template>
    <div>
        <div style="height: 300px; background-color: #fff" @click="startGame">
            <template>
                <el-alert
                        v-if="currentState === 0 || currentState === -1"
                        title="点击此区域开始游戏"
                        type="success"
                        style="height: 100%"
                        description="开始后此处会变成白色, 当颜色变为绿色时按下鼠标!"
                        :closable="false">
                </el-alert>
                <el-alert
                        v-if="currentState === 1"
                        type="info"
                        style="height: 100%"
                        :closable="false">
                </el-alert>
                <el-alert
                        v-if="currentState === 2"
                        type="success"
                        style="height: 100%"
                        effect="dark"
                        :closable="false">
                </el-alert>
            </template>

        </div>
        <el-divider></el-divider>
        <el-button type="info" style="margin-left: 200px" @click="listResult = []">清除成绩</el-button>
        <span>平均成绩:
            <span v-if="listResult.length > 0">{{average}}</span>
        </span>
        <div v-for="(item, index) in listResult" :key="index">
            <span style="margin-left: 100px">成绩:
                <span style="color: #67c23a">{{item}}</span>
                ms</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Reaction",
        data() {
            return {
                currentState: 0,
                timeStart: 0,
                timeEnd: 0,
                listResult: [],
                v: null,
                average: 0,
            }
        },
        methods: {
            startGame() {
                if (this.currentState === 0) {
                    this.currentState = 1;
                    // 0.5 - 10 秒
                    let time = Math.floor(Math.random()*(500 - 10000) + 10000);
                    this.v = setTimeout(() => {
                        this.currentState = 2;
                        this.timeStart = new Date().getTime();
                    }, time);
                } else if (this.currentState === 1) {
                    clearTimeout(this.v);
                    this.$message({
                        message: '你抢跑了属于是',
                        type: 'info'
                    });
                    this.currentState = 0;
                } else if (this.currentState === 2) {
                    clearTimeout(this.v);
                    this.timeEnd = new Date().getTime();
                    this.listResult.splice(0, 0, this.timeEnd - this.timeStart);
                    let avg = 0;
                    this.listResult.forEach(item => {
                        avg += item;
                    });
                    this.average = avg / this.listResult.length;
                    // 防止连点
                    this.currentState = -1;
                    setTimeout(() => {this.currentState = 0}, 1500);
                }
            }
        }
    }
</script>

